.row-content {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.home-section {
  display: flex;
  flex-direction: column;
  row-gap: 50px;
}

.home-header {
  background-image: url(../img/bg/home-header-bg.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  min-height: 350px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.home-header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(13, 7, 17, 0.5);
}
.home-header-content {
  padding: 0 20px;
  text-align: center;
  z-index: 1;
  > h4 {
    color: #ffc010;
    font-family: "Exo 2", serif;
  }
}
.home-content {
  display: flex;
  flex-direction: column;
  row-gap: 70px;
  .about-p {
    padding-top: 0;
  }
}
.home-body-content {
  row-gap: 70px;
}
.home-body-description {
  > p {
    text-align: center;
    color: #fff;
    font-family: "Jua", serif;
    line-height: 25px;
    font-size: 20px;
  }
}
.home-body-choose {
  row-gap: 25px;
}
.home-choose-header {
  text-align: center;
  > p {
    font-family: "Jua", serif;
    font-size: 20px;
  }
  > h4 {
    color: #e59b32;
    font-family: "Jua", serif;
    font-size: 50px;
  }
}

.card-item {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  padding: 0 30px;
  > h5 {
    font-family: "Jua", serif;
    color: #e59b32;
    font-size: 40px;
  }
  > p {
    font-family: "Jua", serif;
    font-weight: 200;
    font-size: 20px;
  }
}

.home-body-choose {
  .swiper {
    width: 440px;
    height: 520px;
  }

  .swiper-slide {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 18px;
    font-size: 22px;
    font-weight: bold;
    color: #fff;
  }

  .swiper-slide:nth-child(1n) {
    background-color: #1b0618;
  }
  .swiper-slide:nth-child(2n) {
    background-color: #290924;
  }

  .swiper-slide:nth-child(3n) {
    background-color: #1b0618;
  }

  .swiper-slide:nth-child(4n) {
    background-color: #290924;
  }

  .swiper-slide:nth-child(5n) {
    background-color: #1b0618;
  }
}

.home-our-approach {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  > h4 {
    color: #e59b32;
    font-family: "Jua", serif;
    font-size: 50px;
  }
  .home-approach-list {
    display: flex;
    flex-direction: column;
    row-gap: 15px;
    .home-approach-item {
      span {
        color: #e59b32;
        font-family: "Jua", serif;
        font-size: 20px;
      }
      p {
        font-family: "Jua", serif;
        font-size: 18px;
      }
    }
  }
}

.home-elevate-business {
  display: flex;
  flex-direction: column;
  row-gap: 25px;
  > h4 {
    text-align: left;
    color: #e59b32;
    font-family: "Jua", serif;
    font-size: 50px;
  }
  > p {
    text-align: center;
    font-family: "Jua", serif;
    font-size: 18px;
  }
}

@media (max-width: 768px) {
  .home-body-choose {
    .swiper {
      width: 340px;
      height: 420px;
    }
  }
  .card-item > h5 {
    font-size: 35px;
  }
  .card-item > p {
    font-size: 18px;
  }
}
@media (max-width: 500px) {
  .home-choose-header > h4,
  .home-our-approach > h4,
  .home-elevate-business > h4 {
    font-size: 30px;
  }
  .home-body-choose {
    .swiper {
      width: 220px;
      height: 300px;
    }
  }
  .card-item > h5 {
    font-size: 20px;
  }
}

@media (max-width: 425px) {
  .home-choose-header > h4,
  .home-our-approach > {
    font-size: 25px;
  }
  .home-body-choose {
    .swiper {
      width: 200px;
      height: 280px;
    }
  }
  .card-item > h5 {
    font-size: 17px;
  }
}
