.header {
  position: relative;
  z-index: 9;
  background: #000;
}

.header-container {
  width: 100%;
  padding: 10px 25px;
}
.header-logo-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-logo {
  padding: 0.5rem;
  img {
    max-width: 130px;
    width: 100%;
    height: 100%;
  }
  @media (max-width: 768px) {
    width: 110px;
  }
}

.header-main-menu {
  display: flex;
  justify-content: center;
  width: 100%;
}
.header-main-menu ul {
  display: flex;
  column-gap: 40px;
  align-items: center;
}

.header-main-menu ul li {
  display: block;
  margin-left: 0;
  transition: 0.5s;
}
.header-main-menu ul li:hover {
  transform: rotate(-10deg);
  a {
    color: #ffc010;
  }
}

.header-main-menu ul li a {
  color: #fff;
  font-size: 1.3vw;
  display: block;
  font-family: "Audiowide", serif;
  font-weight: 500;
  letter-spacing: 1.5px;
  padding: 20px 0;
  position: relative;
  text-transform: uppercase;
  width: max-content;
}

.header-main-menu {
  .menu-active {
    transform: rotate(-10deg);
    a {
      color: #ffc010 !important;
    }
  }
}

.burger-menu-icon {
  display: none;
  padding: 0.5rem;
  @media (max-width: 768px) {
    display: block;
  }
  > p {
    font-size: 30px;
    cursor: pointer;
    color: #fff;
    margin: 0;
    @media (max-width: 768px) {
      display: block;
    }
  }
}

/* Tablet Mobile Menu */

.tablet-mobile-menu {
  .menu-active {
    a {
      color: #ffc010 !important;
    }
  }
}
.offcanvas-menu {
  position: fixed;
  right: 0;
  height: 100%;
  width: 300px;
  z-index: 999;
  background: #00081b;
  top: 0;
  padding: 30px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
}

.offcanvas-menu.active {
  -webkit-transform: translateX(0);
  transform: translateX(0);
}

.menu-close i {
  font-size: 18px;
  color: #fff;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  cursor: pointer;
}

.menu-close:hover i {
  color: #ffc010;
}

.offcanvas-menu ul {
  margin-top: 30px;
}

.offcanvas-menu ul li {
  border-bottom: 1px solid #101c38;
}

.offcanvas-menu ul li a {
  color: #fff;
  font-size: 18px;
  text-transform: capitalize;
  padding: 6px 0;
  display: block;
}

.offcanvas-menu ul li:hover a {
  color: #ffc010;
}

.offcanvas-menu form {
  position: relative;
  margin-top: 30px;
}

.offcanvas-menu form input {
  width: 100%;
  background: none;
  border: 1px solid #2d3547;
  padding: 7px 10px;
  color: #fff;
  border-radius: 8px;
}

.offcanvas-menu form button {
  position: absolute;
  border: none;
  right: 0;
  background: #ffc010;
  padding: 8px 14px;
  top: 0;
  cursor: pointer;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
}

.offcanvas-menu form button i {
  color: #fff;
}

.offcanvas-overly {
  position: fixed;
  background: #000;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 9;
  opacity: 0;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  visibility: hidden;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.offcanvas-overly.active {
  opacity: 0.5;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
  filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
  visibility: visible;
}
