/* Social Media List */

.footer-social-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  width: max-content;
  @media (max-width: 768px) {
    flex-direction: row;
  }
  @media (max-width: 1024px) {
    width: fit-content;
  }
}

.footer-social-wrapper a {
  width: 40px;
  height: 40px;
  background-color: #fff;
  justify-content: center;
  display: flex;
  align-items: center;
  font-size: 18px;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  border: 3px solid #fff;
  z-index: 1;
}

.footer-social-wrapper a .icon {
  position: relative;
  color: #262626;
  transition: 0.5s;
  z-index: 3;
}

.footer-social-wrapper a:hover .icon {
  color: #fff;
  transform: rotateY(360deg);
}

.footer-social-wrapper a:before {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 100%;
  transition: 0.7s;
  z-index: 2;
}

.footer-social-wrapper a:hover:before {
  top: 0;
}

.footer-social-wrapper a.facebook:before {
  background: #4267b2;
}

.footer-social-wrapper a.twitter:before {
  background: #1da1f2;
}

.footer-social-wrapper a.instagram:before {
  background: linear-gradient(
    45deg,
    #f09433 0%,
    #e6683c 25%,
    #dc2743 50%,
    #cc2366 75%,
    #bc1888 100%
  );
}

.footer-social-wrapper a.linkedin:before {
  background: #0e76a8;
}
.footer-social-wrapper a.youtube:before {
  background: #ff0000;
}
.footer-social-wrapper a.twitch:before {
  background: #6441a5;
}